@import '../../variables.scss';

#veloroute.details {
    //background: $veloroute;
    color: $veloroute;
    h1, h2, h3, h4, h5, h6 {
        color: $veloroute;
    }
    .veloroute-section-details {
        h1, h2, h3, h4, h5, h6 {
            color: $veloroute;
        }
    }
}
.veloroute-details {
    margin-bottom: .5em;
    h4 {
        margin: .5em 0;
    }
    &:last-child{
        margin-bottom: 1.5em;
    }
}
ul.veloroute-stops {
    padding: 0;
    li{
        list-style-type: none;
        display: inline;
        span {
            cursor: pointer;
            text-decoration: underline;
            &:hover,
            &.hover{
                color: $veloroute-hover;
            }
        }
    }
}
.veloroute-trainstops {
    padding: 0;
    span.train {
        transform: scale(.85);
        margin: 0 1px 1px 0;
        &:first-child{
            margin-left: .75em;
        }
    }
}