@import '../../variables.scss';
@import '../stateless/box/box.scss';



#fahrtzeit label {
    display: block;
}

input[type="submit"]{
    background-color: rgba(230,230,230,.5);
    padding: $padding-s $padding-l;
    border-radius: 1.5em;
    margin-top: -.5em;
    font-family: 'Oswald', sans-serif;
    letter-spacing: 0.01em;
    transition: all .25s ease;
    transform: scale(1);
    text-transform: uppercase;
    color: $primary;
    font-weight: 700;
    border: 0 solid transparent;
    border-radius: $borderradius;
    display: block;
    min-height: 3.5em;
    -webkit-appearance: none;
    text-align: center;
    cursor: pointer;
    box-shadow: -2px -2px 5px white, inset 1px 1px 2px white, inset -1px -1px 2px rgba(0,0,0,0.15), 2px 2px 5px rgba(0,0,0,0.15);
    text-shadow: 1px 1px 0 white;
    transition: all .3s ease;
    &:hover{
        box-shadow: -1px -1px 3px white, inset 1px 1px 1px white, inset -1px -1px 1px rgba(0,0,0,0.15), 1px 1px 3px rgba(0,0,0,0.15);
        transition: all .3s ease;
    }
    &:active{
        box-shadow: -1px -1px 3px rgba(0,0,0,0.15), inset 1px 1px 1px rgba(0,0,0,0.15), inset -1px -1px 1px white, 1px 1px 3px white;
    }
}
fieldset {
    border: none;
    margin-bottom: $mainpadding;
    &.duration {
        flex-grow: 1;
        display: flex;
        .duration-label {
            min-width: 10em;
        }
    }
}




