@import '../../variables.scss';

#wrapper {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    position: relative;
    height: 100%;
    overflow: hidden;
}
#container {
    position: relative;
    display: flex;
    justify-content: right;
    flex-grow: 1;
}
main {
    position: relative;
    flex-grow: 1;
    order: 1;
    display: flex;
    flex-direction: column;
}
#map-wrapper {
    flex-grow: 1;
    position: relative;
}
aside {
    z-index: 10;
    order: 2;
    pointer-events: none;
    position: relative;
    display: flex;
}